.search-container {
  width: 80%;
  margin: auto;
}
.search-header {
  height: 40px;
  background-color: #607799;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  margin-bottom: 18px;
}
.search-header-title {
  color: azure;
  font-size: 24px;
  font-weight: 500;
  text-align: center;
}

.search-body {
  background-color: #ffff;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
}
