.mui-inner {
  width: 100%;
  border: 0;
  /* margin: 0;
  display: inline-flex; */
  padding: 0;
  position: relative;
  min-width: 0;
  flex-direction: column;
  vertical-align: top;
}
.stepper-content {
  width: 100%;
  height: 80%;
  align-items: center;
  justify-content: center;
}
.center-content {
  align-items: center;
  justify-content: center;
  margin-top: 50px;
}
.text-stepper {
  padding-top: 20px;
  text-align: center;
  font-size: 18px;
  font-weight: 500;
}
.instructions {
  text-align: center;
  font-weight: bold;
  font-size: 18px;
  color: #504f4f;
}
.stepper-container {
  height: 50%;

  border-radius: 8px;
  width: 50%;
}
.lengend-radio {
  margin-bottom: 20px;
  margin-top: 20px;
  text-align: start;
}
.back-button {
  background-color: #607799 !important;
  color: #ffff !important;
  margin-top: 42vh !important;
  margin-left: 50% !important;
}
.next-button {
  background-color: #607799 !important;
  color: #ffff !important;
  float: right;
  margin-top: 42vh !important;
  margin-right: 50% !important;
}

.MuiRadio-colorSecondary.Mui-checked {
  color: #607799 !important;
}
.select-stepper {
  margin-top: 25px;
  width: 100%;
  margin: auto;
  border-radius: 8px;
  align-self: center;
  border: none;
  box-shadow: 2px 2px 10px rgb(0 0 0 / 25%);
}
.success-icon-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50%;
}
.confirm-text {
  font-weight: 700;
}

.container-and-button {
  display: flex;
}
.container-next-and-back {
  width: 25%;
  height: 50%;
}
.MuiTypography-body1 {
  height: 100% !important;
}
