.stepper-content {
  width: 100%;
  height: 80%;
}
.title-stepper {
  padding-top: 20px;
  text-align: center;
  font-size: 18px;
  font-weight: 500;
}

.text-stepper {
  padding-top: 20px;
  font-size: 18px;
  font-weight: 500;
}
.instructions {
  text-align: center;
  font-weight: bold;
  font-size: 18px;
  color: #504f4f;
}
.stepper-container {
  height: 50%;
  border-radius: 8px;
  width: 50%;
}
.lengend-radio {
  margin-bottom: 20px;
  margin-top: 20px;
  text-align: start;
}
.back-button {
  background-color: #607799 !important;
  color: #ffff !important;
  margin-top: 42% !important;
  margin-left: 50% !important;
}
.next-button {
  background-color: #607799 !important;
  color: #ffff !important;
  float: right;
  margin-top: 42% !important;
  margin-right: 50% !important;
}

.MuiRadio-colorSecondary.Mui-checked {
  color: #607799 !important;
}
.select-stepper {
  width: 70%;
  margin: auto;
  align-self: center;
  height: 35px;
}
.success-icon-container {
  flex-direction: column;
  width: 100%;
  height: 50%;
}
.confirm-text {
  font-weight: 700;
  margin: 50px 80px 0px 80px;
}

.container-and-button {
  display: flex;
}
.container-next-and-back {
  width: 25%;
  height: 50%;
}
