.nav-container {
  height: 100%;
  width: 100%;
}
.sidebar-container {
  width: 100%;
  float: left;
  height: 100%;
  background-color: #f8f8f8;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.25);
  align-content: center;
  justify-content: space-evenly;
  display: flex;
  flex-direction: column;
}
.button-sidebar-container {
  width: 124px;
  height: 124px;
  margin-left: auto;
  margin-right: auto;
}

.sidebar-button-text {
  font-size: 11px;
  font-weight: 600;
  color: rgb(85, 85, 85);
}

.sidebar-button-icon {
  font-size: 46px;
  color: #607799;
}
.sidebar-button {
  border: none;
  background-color: #f8f8f8;
  width: 124px;
  height: 124px;
  border-radius: 8px;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.2);
  vertical-align: middle;
}
.sidebar-button:hover {
  background-color: #60779921;
}
