}
.owner-home-button {
  flex-direction: column;
  width: 20%;
}
.owner-home-content {
  flex-direction: column;
  width: 80%;
}
.export-button {
  display: inline-block !important;
  margin-top: 20px;
  margin-left: 81%;
}
.tab-content {
  width: 100%;
}
.PrivateTabIndicator-colorSecondary-5,
